.inputLogin {
  & label {
    font-size: 21px;
    line-height: 24px;
    display: flex;
    flex-direction: column;
    & input {
      margin: 5px 0 20px 0;
      outline: none;
      background-color: rgb(255, 255, 255);
      height: 56px;
      padding: 16px;
      border-radius: 12px;
      font-size: 21px;
      line-height: 24px;
      box-sizing: border-box;
      border: 1px solid rgb(226, 226, 233);
      &:active,
      &:focus {
        border: 1px solid orange;
      }
    }
  }
}
