.errorForm {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: rgb(0, 0, 0, 0.7);
  & .errorBody {
    width: 420px;
    height: 150px;
    padding: 32px;
    background-color: rgb(243, 243, 247);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    &__title {
      font-size: 21px;
      font-weight: 500;
    }
    &__button {
      height: 62px;
      padding: 12px 24px;
      font-size: 24px;
      line-height: 27px;
      background-color: rgb(255, 105, 0);
      color: rgb(255, 255, 255);
      cursor: pointer;
      outline: none;
      border: none;
      border-radius: 9999px;
      text-align: center;
      font-weight: 500;
    }
  }
}
