.loginButton {
  height: 62px;
  padding: 12px 24px;
  font-size: 24px;
  line-height: 27px;
  background-color: rgb(226, 226, 233);
  color: rgb(171, 173, 186);
  cursor: not-allowed;
  outline: none;
  border: none;
  border-radius: 9999px;
  text-align: center;
  font-weight: 500;
  &.active {
    background-color: rgb(255, 105, 0);
    color: rgb(255, 255, 255);
    cursor: pointer;
  }
}
